<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js";
  export let activityResult;
  const dispatch = new createEventDispatcher();

  let comment;
  let percentage;

  onMount(async () => {
    let i = 0;
    const tmp = activityResult;
    activityResult = undefined;
    activityResult = tmp;
  });

  async function checkActivityResult() {
    console.log(activityResult);
    console.log({
      id: activityResult.id,
      comment,
      percentage,
    });
    await fetch("/api/v1/activityResult/check", {
      headers: getDefaultHeaders(),
      method: "PUT",
      body: JSON.stringify({
        id: activityResult.id,
        comment,
        percentage,
      }),
    }).then(async () => await dispatch("refreshActivityResults"));
  }
</script>

{activityResult.activity.title} by {activityResult.student.userInfo.user
  .name}<br />
<label
  >Percentage:
  <input
    class="form-control"
    type="number"
    min="0"
    max="100"
    id="percentageInput"
    bind:value={percentage}
  />
</label>
<label>
  Comment:
  <input
    class="form-control"
    type="text"
    id="commentInput"
    bind:value={comment}
  />
</label>
<br />
<div class="modal-footer ">
  <button
    class="btn btn-success"
    on:click={async () => await checkActivityResult()}>Submit</button
  >
</div>
