<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js";
  const dispatch = new createEventDispatcher();
  let quizResults = [];
  export let canSelect = true;
  export let canEdit;
  export let canDelete;

  onMount(async () => {
    await refreshQuizResults();
  });

  export async function refreshQuizResults() {
    quizResults = await fetch("/api/v1/quizResult", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());
  }
</script>

{#if quizResults.length > 0}
  <table class="table table-striped">
    <thead>
      <tr>
        <th>Quiz</th>
        <th>Student</th>
        <th>Percentage</th>
        <th>Comment</th>
        <th>Action</th>
      </tr>
    </thead>
    <tbody>
      {#each quizResults as quizResult}
        <tr>
          <td>{quizResult.quiz.title}</td>
          <td>{quizResult.student.userInfo.user.name}</td>
          <td>{quizResult.percentage}</td>
          <td>{quizResult.comment}</td>
          <td>
            {#if canSelect}
              <button
                class="btn btn-primary"
                on:click={async () =>
                  await dispatch("selectQuizResult", quizResult)}>Select</button
              >
            {/if}
            {#if canEdit}
              <button
                style="padding: 0; border: none; background: none;"
                on:click={async () =>
                  await dispatch("editQuizResult", quizResult)}
                ><svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="feather feather-edit-2 align-middle"
                  ><path
                    d="M17 3a2.828 2.828 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3z"
                  /></svg
                ></button
              >
            {/if}
            {#if canDelete}
              <button
                style="padding: 0; border: none; background: none;"
                on:click={async () =>
                  await dispatch("deleteQuizResult", quizResult)}
                ><svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="feather feather-trash align-middle"
                  ><polyline points="3 6 5 6 21 6" /><path
                    d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
                  /></svg
                ></button
              >
            {/if}
          </td>
        </tr>
      {/each}
    </tbody>
  </table>
{/if}
