<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { formatDateTime, getDefaultHeaders } from "../utils.js";
  const dispatch = new createEventDispatcher();
  let anouncements = [];

  onMount(async () => {
    await refreshAnouncements();
  });

  export async function refreshAnouncements() {
    anouncements = await fetch("/api/v1/anouncement", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());
  }
</script>

{#if anouncements.length > 0}
  {#each anouncements.sort((b, a) => new Date(a.dateCreated) - new Date(b.dateCreated)) as anouncement}
    <div style="margin: 1rem;">
      <h3 style="margin:auto">{anouncement.title}</h3>
      <h6 style="margin:auto">{formatDateTime(anouncement.dateCreated)}</h6>
      <div>
        <p>{anouncement.description}</p>
      </div>
    </div>
    <br />
  {/each}
{/if}
