<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { getDefaultHeaders } from "../utils.js";
  const dispatch = new createEventDispatcher();

  export let activityResult;
  let students = [];
  let activity;
  let student;
  let comment;
  let percentage;
  let files = [];

  onMount(async () => {
    activity = activityResult.activity;
    let i = 0;
    const tmp = activity;
    activity = undefined;
    activity = tmp;
    students = await fetch("/api/v1/student", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());
    student = activityResult.student;
    comment = activityResult.comment;
    percentage = activityResult.percentage;
  });

  onMount(async () => {});

  async function updateActivityResults() {
    const formData = new FormData();
    formData.append("id", activityResult.id);
    formData.append("studentId", student.id);
    formData.append("comment", comment);
    formData.append("percentage", percentage);
    formData.append("file", files[0]);
    await fetch("/api/v1/activityResult/uploadFile", {
      headers: {
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
      method: "PUT",
      body: formData,
    }).then(async () => await dispatch("refreshActivityResults"));
  }
</script>

{activity?.title} ({activity?.subject.name}) by {activity?.teacher.userInfo.user
  .name}<br />

<label>
  Student:
  <select class="form-control" bind:value={student}>
    <option value="">Select a Student</option>
    {#each students as student}
      <option value={student}>{student.userInfo.user.name}</option>
    {/each}
  </select>
</label>

<br />
<label>
  Percentage:
  <input
    class="form-control"
    type="number"
    min="0"
    max="100"
    id="percentageInput"
    bind:value={percentage}
  /></label
>
<label>
  Comment:
  <input
    class="form-control"
    type="text"
    id="commentInput"
    bind:value={comment}
  /></label
>
<div class="mb-3 row">
  <label
    >File: <input
      class="form-control"
      type="file"
      name="file"
      bind:files
    /></label
  >
</div>
<br />
<button on:click={async () => await updateActivityResults()}>Submit</button>
