<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js";
  const dispatch = new createEventDispatcher();
  export let quiz;

  let selectedItem;
  let teachers = [];
  let subjects = [];
  let teacher;
  let subject;
  let title;
  let timeLimit;
  let dateOfSubmission;
  let items = [];
  let itemNumber;
  let type;
  let question;
  let answer;
  let incorrectAnswers = [];
  let incorrectAnswerInput;
  let isTeacher;

  onMount(async () => {
    teachers = await fetch("/api/v1/teacher", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());
    subjects = await fetch("/api/v1/subject", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());
    title = quiz.title;
    timeLimit = quiz.timeLimit;
    teacher = quiz.teacher;
    subject = quiz.subject;
    items = quiz.items;
    let userInfo = getCurrentUserInfo();

    // if (userInfo?.user?.role == "Teacher") {
    //   teacher = teachers.filter(
    //     (t) => t.userInfo.user.id == userInfo.user.id
    //   )[0];
    //   isTeacher = true;
    // }
  });

  async function updateQuiz() {
    await fetch("/api/v1/quiz", {
      headers: getDefaultHeaders(),
      method: "PUT",
      body: JSON.stringify({
        id: quiz.id,
        title,
        timeLimit,
        dateOfSubmission: new Date(dateOfSubmission).toISOString(),
        teacher: {
          id: teacher.id,
        },
        subject: {
          id: subject.id,
        },
        items,
      }),
    }).then(async () => await dispatch("refreshQuizzes"));
  }

  async function deleteIncorrectAnswer(incorrectAnswer) {
    incorrectAnswers = incorrectAnswers.filter((m) => m != incorrectAnswer);
  }

  async function addIncorrectAnswer() {
    incorrectAnswers.push(incorrectAnswerInput);
    let tmp = incorrectAnswers;
    incorrectAnswers = [];
    incorrectAnswers = tmp;
    incorrectAnswerInput = "";
  }

  async function addItem() {
    items.push({
      question,
      answer,
      type,
      incorrectAnswers,
    });
    let tmp = items;
    question = "";
    answer = "";
    incorrectAnswers = [];
    items = [];
    items = tmp;
  }

  async function updateItem() {
    items[itemNumber - 1] = {
      question,
      answer,
      type,
      incorrectAnswers,
    };
    let tmp = items;
    question = "";
    answer = "";
    type = "";
    incorrectAnswers = [];
    items = [];
    items = tmp;
  }

  async function editItem(item, number) {
    itemNumber = number + 1;
    selectedItem = item;
    question = item.question;
    answer = item.answer;
    type = item.type;
    incorrectAnswers = item.incorrectAnswers;
  }

  async function deleteItem(item) {
    items = items.filter((i) => i != item);
  }
</script>

<div class="mb-3 row">
  <label
    >Title: <input class="form-control" type="text" bind:value={title} /></label
  >
</div>
{#if !isTeacher}
  <div class="mb-3 row">
    <label>
      Teacher:
      <select class="form-control" bind:value={teacher}>
        <option value="">Select a Teacher</option>
        {#each teachers as teacher}
          <option value={teacher}>{teacher.userInfo.user.name}</option>
        {/each}
      </select>
    </label>
  </div>
{/if}
<div class="mb-3 row">
  <label>
    Subject:
    <select class="form-control" bind:value={subject}>
      <option value="">Select a Subject</option>
      {#each subjects as subject}
        <option value={subject}>{subject.name}</option>
      {/each}
    </select>
  </label>
</div>
<div class="mb-3 row">
  <label
    >Time limit (seconds): <input
      class="form-control"
      type="number"
      bind:value={timeLimit}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Date of Submission: <input
      class="form-control"
      type="datetime-local"
      bind:value={dateOfSubmission}
    /></label
  >
</div>
<br />
<div class="mb-3 row">
  <label
    >No.: <input
      class="form-control"
      readonly
      type="number"
      bind:value={itemNumber}
    /></label
  >
</div>
<div class="mb-3 row">
  <label>
    Type:
    <select class="form-control" bind:value={type}>
      <option value="Multiple choices">Multiple choices</option>
      <option value="True or False">True or False</option>
      <option value="Matching type">Matching type</option>
      <option value="Fill in the blank">Fill in the blank</option>
    </select>
  </label>
</div>
<div class="mb-3 row">
  <label>Question: <input class="form-control" bind:value={question} /></label>
</div>
{#if type === "True or False"}
  <div style="display: flex; flex-wrap: wrap;" class="m-3">
    <label class="form-check-label m-3"
      ><input
        class="form-check-input"
        bind:group={answer}
        type="radio"
        value="true"
      />True</label
    >
    <label class="form-check-label m-3"
      ><input
        class="form-check-input"
        bind:group={answer}
        type="radio"
        value="false"
      />False</label
    >
  </div>
{:else}
  <div class="mb-3 row">
    <label>Answer: <input class="form-control" bind:value={answer} /></label>
  </div>
{/if}
{#if type === "Multiple choices"}
  <div class="mb-3 row">
    <label>
      Incorrect answer:
      <input class="form-control" bind:value={incorrectAnswerInput} />
      <button
        class="btn btn-success"
        on:click={async () => await addIncorrectAnswer()}
        >Add incorrect answer</button
      >
    </label>
    <br />
  </div>
  {#if incorrectAnswers.length > 0}
    <table class="table table-striped">
      <thead>
        <tr>
          <th>Answer</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        {#each incorrectAnswers as incorrectAnswer}
          <tr>
            <td>{incorrectAnswer}</td>
            <td
              ><button
                class="btn btn-info"
                on:click={async () =>
                  await deleteIncorrectAnswer(incorrectAnswer)}>Delete</button
              ></td
            >
          </tr>
        {/each}
      </tbody>
    </table>
  {/if}
{/if}
<button class="btn btn-success m-3" on:click={async () => await addItem()}
  >Add Item</button
>
<button class="btn btn-danger m-3" on:click={async () => await updateItem()}
  >Update Item</button
>
<br />
{#if items.length > 0}
  <table class="table table-striped">
    <thead>
      <tr>
        <th>No. </th>
        <th>Type</th>
        <th>Question</th>
        <th>Answer</th>
        <th>Incorrect answers</th>
        <th>Actions</th>
      </tr>
    </thead>
    <tbody>
      {#each items as item, i}
        <tr>
          <td>{i + 1} </td>
          <td>{item.type}</td>
          <td>{item.question}</td>
          <td>{item.answer}</td>
          <td
            >{item.type === "Multiple choices" ? item.incorrectAnswers : ""}</td
          >
          <td>
            <button
              style="padding: 0; border: none; background: none;"
              on:click={async () => await editItem(item, i)}
              ><svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
                class="feather feather-edit-2 align-middle"
                ><path
                  d="M17 3a2.828 2.828 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3z"
                /></svg
              ></button
            >
            <button
              style="padding: 0; border: none; background: none;"
              on:click={async () => await deleteItem(item)}
              ><svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
                class="feather feather-trash align-middle"
                ><polyline points="3 6 5 6 21 6" /><path
                  d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
                /></svg
              ></button
            ></td
          >
        </tr>
      {/each}
    </tbody>
  </table>
{/if}
<br />
<div class="modal-footer ">
  <button class="btn btn-warning" on:click={async () => await updateQuiz()}
    >Submit</button
  >
</div>
